import "./CSS/Loginform.css";
const Login = (props) => {
    const { id, pwd, changeId, changePwd } = props;
    return (
      <div>
        <div className="logintext">
          <div className="bigText" id="selfintro_header" style={{height:'100px'}}><br /></div>
                .
        </div>
        <table  align="center"  className="logininput">
          <tr>
            <td style={{width:'100px'}}></td>
            <td>
              <input type="text" class="blank" size="12" value={id} onChange={changeId}/>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <input type="password" class="blank" size="12" value={pwd} onChange={changePwd} />
            </td>
          </tr>
        </table>
        
        <div className="bigText backgroundstyle_opa loginform"></div>
      </div>
    );
  };
  export default Login;
  