
import "../CSS/index.css";
import React, { useState } from 'react';

const Index = () => {
  const whoami = [" ", "", "", ""];
  const describe = [
    "     .",
    "  20 .",
    "    .",
    "     .",
  ];
  const image = [
      '/image/index1.jpg',
      '/image/index2.jpg',
      '/image/index3.jpg',
      '/image/index4.jpg'
  ]

  const [currentword, nextword] = useState(0);

  const gotonext = () => {
    nextword((currentword) => (currentword + 1) % whoami.length);
  };

  return (
    <div>
      <h1 id="selfintro_header">    !</h1>
      <br />
         ?<br />
      <div className='middle_text'> {whoami[currentword]}.</div>
      <div ><img src={image[currentword]} alt=''className="index_image"></img></div>
      
      <br />
      <button id="next_button" onClick={gotonext}> </button>
      <p className='middle_text'>{describe[currentword]}</p>
               !
    </div>
  );
};

export default Index;